import React, { Component } from 'react'
import './css.css';
import Web3 from 'web3';

class Book extends Component {
constructor(props){
    super()
    this.openShop = this.openShop.bind(this);
}
    componentDidMount(){
        this.loadShops()
    }

    async openShop(shop){
        window.history.pushState('show_shop', 'Shop Display', '/?shop='+shop);
        this.props.jumptoshop()
    }

    async loadShops(){

        let openShop=this.openShop
        await window.ethereum.enable().then(async function (accounts) {
    
            const { REACT_APP_BOOKABI } = await process.env;
            const { REACT_APP_BOOK } = await  process.env;
            const book = new web3.eth.Contract(JSON.parse(REACT_APP_BOOKABI), REACT_APP_BOOK) 
            let res=await book.methods.door(0).call()
            let count=res[1]
            const { REACT_APP_METAABI } = await process.env;
            const { REACT_APP_META } = await  process.env;
            const metadata = new web3.eth.Contract(JSON.parse(REACT_APP_METAABI), REACT_APP_META) 
            document.getElementById("book_sublabel").innerHTML="All Shops List ("+count+")"
            for(let i=0;i<count;i++){
                let module_address=await book.methods.door(i).call()
                let mtdt=await metadata.methods.meta(module_address[0]).call()
                let back="https://images-wixmp-ed30a86b8c4ca887773594c2.wixmp.com/f/27592dd5-bdf2-4e90-9e17-852077de3dd0/d2aytcz-fd1a408a-541d-465d-9f8d-9c662b5ba6db.jpg/v1/fill/w_900,h_600,q_75,strp/fading_blue_grunge_by_dyingbeautystock.jpg?token=eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9..JSbyhtv1aQd2O8AzG2x1UTZLfyNdtwGNtNSgzKCDFTE"
                if(mtdt.length>0){
                    console.log("MTDT",mtdt)
                    fetch(formatURL(mtdt)).then(response => response.json()).then(
                    response => {
                        console.log(JSON.stringify(response))
    
                        back=response[0]["image"]
                        //document.getElementById("Shop_header_image").style.backgroundImage='url('+response[0]["image"]+')'
                        let mainDiv = document.createElement('div');
                        let iDiv = document.createElement('div');
                        let backDiv = document.createElement('div');
                        let iname = document.createElement('div');
                        iDiv.id = 'shop_'+i;
                        iDiv.className = 'shop_link';
                        mainDiv.className = 'shop_maindiv';
                        backDiv.className = 'shop_backdiv';
                        iname.innerHTML=response[0]["name"]+" "+i;
                        iname.style.margin="100px 20px 0 0"
                        iname.style.fontSize="30px"
                        iname.style.float="right"
                        iDiv.style.backgroundImage='url('+back+')'
                        iDiv.style.backgroundSize='cover'
                        iDiv.appendChild(iname)
                        mainDiv.appendChild(backDiv)
                        mainDiv.appendChild(iDiv)
                        mainDiv.append(response[0]["description"])
                        mainDiv.addEventListener("click",  () => window.open("/?shop="+module_address[0]))
                        document.getElementById("book_board").appendChild(mainDiv)
       
                    })
                }else{
                let iDiv = document.createElement('div');
                iDiv.id = 'shop_'+i;
                iDiv.className = 'shop_link_empty';
                let iname = document.createElement('div');
                iname.style.margin="100px 0 0 0"
                iname.innerHTML=""+i+" "+module_address[0]
                iname.style.fontSize="20px"
                iDiv.appendChild(iname)
                iDiv.style.backgroundImage='url('+back+')'
                iDiv.style.backgroundSize='cover'
                iDiv.addEventListener("click", () => window.open("/?shop="+module_address[0]))
                document.getElementById("book_board").appendChild(iDiv)
                }
                
            }
            })

    }
   
    

 


    render(){
        console.log(this.props.update) 
        return ( 
        <div>

        <div id="book_flyer">
            <div id="splash_mainlabel"> SmartShop Global Book</div>
            <div id="book_sublabel">All Shops List</div>
            <div id="book_board"></div>
        </div>
      
        </div>
        );
    }


}

function jumpsocial(add){
    window.open(add)
}


function fire(){}


function formatURL(url){
    return("https://gateway.ipfs.io/ipfs/"+url);
  }


function getUrlVars() {
    var vars = {};
    var parts = window.location.href.replace(/[?&]+([^=&]+)=([^&]*)/gi, function(m,key,value) {
        vars[key] = value;
    });
    return vars;
}



const web3 = new Web3(Web3.givenProvider || "http://localhost:8545")



export default Book